nav{
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 55px;
    background-color: rgb(255, 255, 255);
    -webkit-box-shadow: 0 0 rgba(0,0,0,.12);
            box-shadow: 0 0 rgba(0,0,0,.12);
    -webkit-transition: height 0.3s ease-in-out,-webkit-box-shadow 0.3s ease-in-out;
    transition: height 0.3s ease-in-out,-webkit-box-shadow 0.3s ease-in-out;
    -o-transition: box-shadow 0.3s ease-in-out,height 0.3s ease-in-out;
    transition: box-shadow 0.3s ease-in-out,height 0.3s ease-in-out;
    transition: box-shadow 0.3s ease-in-out,height 0.3s ease-in-out,-webkit-box-shadow 0.3s ease-in-out;
    z-index: 1;
    top:0;
}
nav h2{
    font-family: 'Pacifico', cursive;
    font-weight: 500;

}
nav ul{
    list-style: none;
}
nav ul li{
    padding: 5px;
}
nav ul li a{
     text-decoration: none;
}
nav.shadow{
    height: 45px;
     -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.12);
             box-shadow: 0 2px 5px 0 rgba(0,0,0,.12);    
}


