 .twoToneButton {
  position: relative;
  border: none;
  margin: 5px 2px;
  background-color: #f31212;
    outline: none;
    padding: 7px 15px;
    cursor: pointer;
    font-weight: 500;
    border-radius: 50px; 
    -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.16),0 2px 10px 0 rgba(0,0,0,.12); 
            box-shadow: 0 2px 5px 0 rgba(0,0,0,.16),0 2px 10px 0 rgba(0,0,0,.12);
    -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-transform 0.15s ease-in-out, -webkit-box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-transform 0.15s ease-in-out, -webkit-box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    -o-transition: transform 0.15s ease-in-out, color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: transform 0.15s ease-in-out, color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: transform 0.15s ease-in-out, color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out,box-shadow .15s ease-in-out, -webkit-transform 0.15s ease-in-out, -webkit-box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    color: #fff;
}
 .twoToneButton:hover, .twoToneButton:active {
   position: relative;
cursor: pointer;
overflow: hidden;
-webkit-user-select: none;
        user-select: none;
-moz-user-select: none;
-ms-user-select: none;
user-select: none;
z-index: 1;
-webkit-box-shadow:0 5px 11px 0 rgba(0,0,0,.18),0 4px 15px 0 rgba(0,0,0,.15);
        box-shadow:0 5px 11px 0 rgba(0,0,0,.18),0 4px 15px 0 rgba(0,0,0,.15);
}
 .twoToneButton.spinning {
  padding-right: 40px;
  cursor:not-allowed;
  background-color:rgba(255, 0, 0, 0.7); 
}

 .twoToneButton.spinning:before {
  content: "";
  width: 0px;
  height: 0px;
  border-radius: 50%;
  right: 6px;
  top: 45%;
  position: absolute;
  border: 2px solid #727272;
  border-right: 2px solid #fbff00;
  -webkit-animation: rotate360 .5s infinite linear, exist .1s forwards ease ;
          animation: rotate360 .5s infinite linear, exist .1s forwards ease ;
}
@-webkit-keyframes rotate360 {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate360 {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes exist {
  100% {
    width: 15px;
    height: 15px;
    margin: -8px 5px 0 0;
  }
}
@keyframes exist {
  100% {
    width: 15px;
    height: 15px;
    margin: -8px 5px 0 0;
  }
}



